import SmoothScroll from 'smooth-scroll';

const navChilds = Array.from(document.querySelector('.header__nav').children);

navChilds.forEach((a) => {
    a.addEventListener('mouseenter', ({ target }) => {
        navChilds.forEach((a) => {
            if (a !== target) a.classList.add('darken');
        });
    });

    a.addEventListener('mouseleave', ({ target }) => {
        navChilds.forEach((a) => {
            if (a !== target) a.classList.remove('darken');
        });
    });
});

var scroll = new SmoothScroll('a[href*="#"]');






