const CARD_DATA = [
	{
		bg: "portfolio1",
		name: "Reaction game",
		link: "https://qxie11.github.io/test-game/",
		github: "https://github.com/qxie11/test-game",
		descr: 'Hello '
	},
	{
		bg: "portfolio2",
		name: "Quiz",
		link: "https://qxie11.github.io/quiz/",
		github: "https://github.com/qxie11/quiz",
		descr: 'fdsa'
	},
	{
		bg: "portfolio3",
		name: "Weather app",
		link: "https://qxie11.github.io/weather/",
		github: "https://github.com/qxie11/weather",
		descr: 'fdsa'
	},
	{
		bg: "portfolio4",
		name: "Todo list",
		link: false,
		github: "https://github.com/qxie11/react-todo",
		descr: 'fdsa'
	},
	{
		bg: "portfolio5",
		name: "Covid monitoring",
		link: "https://qxie11.github.io/covid/",
		github: "https://github.com/qxie11/covid",
		descr: 'fdsa'
	},
];

const portfolio_area = document.querySelector('.portfolio__area');

class Description {
	constructor(descr, name) {
		this.descr = descr;
		this.name = name;
	}

	render() {
		const description = this.createElement('div', 'description'),
			descrArea = this.createElement('div', 'description__area'),
			descrChapter = this.createElement('h1', 'description__chapter'),
			descrClose = this.createElement('div', 'description__close'),
			descrText = this.createElement('p', 'description__text');
		descrText.textContent = this.descr;
		descrArea.appendChild(descrText);
		descrChapter.textContent = this.name;
		description.appendChild(descrChapter);
		description.appendChild(descrArea);
		description.appendChild(descrClose);
		document.body.appendChild(description);

		const allDescrButoons = document.querySelectorAll('.portfolio__card-descr');

		Array.from(allDescrButoons).forEach((button) => {
			button.setAttribute('disabled', 'disabled');
		});

		descrClose.addEventListener('click', () => {
			description.remove();
			Array.from(allDescrButoons).forEach((button) => {
				button.removeAttribute('disabled', 'disabled');
			});
		});
	}

	createElement(elementTag, elementClass) {
		const element = document.createElement(elementTag);

		if (elementClass) {
			element.classList.add(elementClass);
		}

		return element;
	}
}


class Card {
	constructor(bg, name, link, github, descr) {
		this.bg = bg;
		this.name = name;
		this.link = link;
		this.github = github;
		this.descr = descr;
	}

	render() {
		const card = this.createElement('div', 'portfolio__card');
		const front = this.createElement('div', 'portfolio__card-front');
		front.style.backgroundImage = `url('../public/img/portfolio/${this.bg}.jpg')`;
		const name = this.createElement('span', 'portfolio__card-name');
		name.textContent = this.name;
		front.appendChild(name);
		const back = this.createElement('div', 'portfolio__card-back');
		const link = this.createElement('button', 'portfolio__card-link');
		link.addEventListener('click', this.redirect(this.link));
		const github = this.createElement('button', 'portfolio__card-github');
		github.addEventListener('click', this.redirect(this.github));
		const descr = this.createElement('button', 'portfolio__card-descr');
		descr.addEventListener('click', () => {
			new Description(this.descr, this.name).render();
		});
		this.link && back.appendChild(link);
		back.appendChild(github);
		back.appendChild(descr);
		card.appendChild(front);
		card.appendChild(back);
		portfolio_area.appendChild(card);
	}

	redirect(link) {
		return function () {
			window.open(link, "_blank");
		};
	}

	createElement(elementTag, elementClass) {
		const element = document.createElement(elementTag);

		if (elementClass) {
			element.classList.add(elementClass);
		}

		return element;
	}
}


CARD_DATA.forEach(({ bg, name, link, github, descr }) => {
	new Card(bg, name, link, github, descr).render();
});