const area = document.querySelector('.about__area');
const ABOUT_ME = [
    "Evgeniy, 19 years old. I've graduated from Odessa Technical College. Previously, I did not work as a web-programmer. If you accept a job, I’m ready to switch to distance learning. I am fond of my profession for about two years. Not a single day of mine passes without self-development, in particular in the field of web development. I hope that I have achieved sufficient success to test myself in professional development.",
    "In addition to the prevailing advantages, there are also disadvantages, most of all bothering: impatience and restlessness, with which I constantly struggle.",
    "In addition to the main profession, I am interested in other things, for example: history, English, economics, politics."
];

class About_P {
    constructor(about) {
        this.about = about;
    }

    render() {
        const about_p = document.createElement('p');
        about_p.classList.add('about__area-p');
        about_p.textContent = this.about;
        area.appendChild(about_p);
    }
}

for (let p of ABOUT_ME) {
    new About_P(p).render();
}

