const hithere = document.querySelector('.header__hi-there'),
    arm1 = document.querySelector('.header__hi-there-arm1'),
    arm2 = document.querySelector('.header__hi-there-arm2');

const anime = () => {
    arm1.classList.add('active');
    arm2.classList.add('active');
    hithere.removeEventListener('click', anime);

    setTimeout(() => {
        arm1.classList.remove('active');
        arm2.classList.remove('active');
        hithere.addEventListener('click', anime);
    }, 1000);
};

hithere.addEventListener('click', anime);

