const interview = document.querySelector('.faq__interview');
const QUES_ANSW = [
    {
        question: "What is your name?",
        answer: "My name is Ponomarev Evgeniy."
    },
    {
        question: "How old are you?",
        answer: "I'm 19 years old."
    },
    {
        question: "Where do you live?",
        answer: "Odessa, Suvorovskiy region. Possible relocation to another city."
    },
    {
        question: "What education do you have?",
        answer: "I've graduated Odessa's Technical College with faculty 'Web-development and web-design'. Have an incompleted higher education. Intend to go Odessa national academy of food technologies this year."
    },
    {
        question: "What advantages and disadvantages can you highlight in your character?",
        answer: "I am as motivated as restless. My enthusiasm sweeps just as fast, it fades just as fast. It’s not easy for me to systematically do one thing for a long amount of time, with this I constantly struggle."
    },
    {
        question: "How many years have you been doing front-end development?",
        answer: "About three years(non-commercial). The most significant successes have been achieved in the last year. I consolidated my knowledge of HTML, CSS, JS and switched to technology on the path above. Well learned React, ES6. At the initial level, I know Typescript. In the future I will learn Python, NodeJS, PHP. I believe that now is the time for my professional career."
    },
    {
        question: "Have you worked officially up to this point?",
        answer: "As Front-end developer - No."
    },
    {
        question: "What is your level of knowledge English language?",
        answer: "Upper Intermediate(B2). I can keep up the conversation on non-complex topics."
    },
    {
        question: "Have you had any team experience? Are you predisposed to this?",
        answer: "No, there was no experience working in a team. I can’t say that I am the most sociable and can easily find common topics with anyone for conversation, but for the sake of a common cause, I can successfully integrate into the team."
    },
    {
        question: "What salary do you want to receive?",
        answer: "~400$ for begining."
    },
    {
        question: "Are there any hobbies besides the main specification?",
        answer: "History, English, economics, politics."
    },
];

class Question_Answer {
    constructor(answer, question) {
        this.answer = `Answer: ${answer}`;
        this.question = `Question: ${question}`;
    }

    render() {
        const answ_ques_wrap = document.createElement('div');
        answ_ques_wrap.classList.add('faq__interview-wrap');
        const question = document.createElement('p');
        question.classList.add('faq__interview-question');
        question.textContent = this.question;
        const answer = document.createElement('p');
        answer.classList.add('faq__interview-answer');
        answer.textContent = this.answer;
        answ_ques_wrap.append(question);
        answ_ques_wrap.append(answer);
        interview.appendChild(answ_ques_wrap);
    }
}

for (let { answer, question } of QUES_ANSW) {
    new Question_Answer(answer, question).render();
}

