class Link {
	constructor(link, img_path, color) {
		this.link = link;
		this.img_path = img_path;
		this.color = color;
	}

	render() {
		const link = this.createElement('div', 'links__wrapper-link');
		link.addEventListener('click', () => {
			window.open(this.link);
		});
		const img = this.createElement('img');
		img.setAttribute('src', `../public/img/links/links__${this.img_path}`);
		img.setAttribute('alt', this.img_path.slice(0, this.img_path.indexOf('.')));
		link.append(img);
		const wrapper = document.querySelector('.links__wrapper');
		wrapper.append(link);
	}

	createElement(elementTag, elementClass) {
		const element = document.createElement(elementTag);

		if (elementClass) {
			element.classList.add(elementClass);
		}

		return element;
	}
}

LINKS_DATA.forEach(({ link, img_path, color }) => {
	new Link(link, img_path, color).render();
});