import TagCloud from 'TagCloud';

const container = '.tag-cloud';
const texts = [
    '.', '.', '.',
    '.', '.', '.',
    '.', '.', '.'
];

let radius = window.innerWidth > 655 ? 350 : 200;

const options = {
    radius,
    maxSpeed: 'slow',
    initSpeed: 'slow'
};

TagCloud(container, texts, options);